import { Route, Switch } from 'react-router-dom';
import App from 'containers/App';

/* eslint-disable arrow-body-style */
export default () => {
    console.log('adasdsa')
    return (
        <Switch>
            <Route exact path="/" component={App} />
        </Switch>
    );
};



